/* Load common sass variables */
@import 'variables';

/* Stylesheet */
header {
  padding-right: 0px;
}
.navtopright {
  position:absolute;
  font-size:11pt;
  top:4px;
  right:5px;
  text-align: right;
  color: white;
}
.navtopright a {
  padding-right:10px;
}
.footer_content {
  font-size:11pt;
  margin-top:10px;
  margin-left:20px;
  margin-right:20px;
}
.site-content {
  margin-bottom: 80pt;
}
.blurb {
  margin-bottom:40pt;
  text-align:justify;
}
.balloon {
  float:left;
  padding-top:10px;
  margin-right:12px;
}
.mdc-chip-set {
  width: fit-content;
  width: -moz-fit-content;
  width: -webkit-fit-content;
  margin-right:0pt;
  margin-left: auto;
}
.mdc-toolbar {
  padding:0pt;
}
.mdc-toolbar__row {
  min-height: 100px;
  padding-top:30px;
  background-image:radial-gradient(500px 500px,rgba(255,255,255,0.4),$mdc-theme-primary);
}
.mdc-typography--caption {
  text-align:center;
  margin-top:16px;
  font-size: 12pt;
  font-weight: 700;
}
.mdc-typography--display3 {
  padding-top:50px;
  padding-bottom:10px;
  top:70px;
  z-index: 2;
}
.mdc-typography--body1 {
  font-size: 0.875rem;
}
.mdc-typography--headline {
  font-size: 1.3rem;
  line-height: 2rem;
  letter-spacing: -0.02em;
}
.embedded-video {
  display:block;
  margin:auto;
}
.white {
  background-color: white;
}
.icon {
  font-size:24pt;
}
.stats {
  overflow:visible;
  display:block;
  margin:auto;
}
.count {
  font-size: 32pt;
}
.school-logo {
  width:100%;
  filter: grayscale(1);
  max-width:200px;
  display: block;
  margin: auto;
  padding: 20px;
  cursor: pointer;
}
.school-logo:hover {
  filter: grayscale(0);
  transition: filter 500ms;
}
.text-link {
  text-decoration: underline;
  text-decoration-color: $mdc-theme-primary;
}
.text-link:hover {
  color: $mdc-theme-primary;
  transition: color 500ms;
}
.pillar-text {
  margin-left:50px;
  margin-right:20px;
  margin-top:10px;
  display:none;
  font-style: italic;
  text-align:left;
}
blockquote {
  font-family: 'Cardo', serif;
  font-size:20pt;
  font-style:italic;
}
.timeline-axis line{
  stroke: gray;
  stroke-width: 1.5px;
}
.timeline-axis text {
  font-family: 'Montserrat', sans-serif;
  font-size: 11pt;
  fill: gray;
}
.timeline-axis path {
  stroke: gray;
  stroke-width: 1.5px;
}
#history {
  overflow:visible;
  margin-bottom:10px;
}
#pillars {
   overflow:visible;
   display:block;
   margin:auto;
}
#scrollprompt {
  color: white;
  position:absolute;
  cursor: pointer;
  bottom:0px;
  left:50%;
}
#animation {
  display:block;
  margin:auto;
  overflow:visible;
}
@media (max-width:450px) {
  .blurb {
    margin-bottom:20pt;
  }
  .mdc-typography--body1 {
    font-size: 0.8rem;
    line-height: 1.1rem;
  }
  .mdc-typography--headline {
    font-size: 0.9rem;
    line-height: 1.3rem;
  }
  .mdc-typography--display3 {
    padding-top:30px;
    padding-bottom:0px;
    font-size: 2rem;
    line-height: 3rem;
  }
  .mdc-typography--caption {
    margin-top: 10px;
    font-size:14px;
  }
  .mdc-chip__text {
    font-size: 10pt;
  }
  .navtopright {
    font-size:10pt;
    right:0px;
  }
  .count {
    font-size: 20pt;
  }
  footer .icon {
    font-size: 24px;
  }
  .footer_content {
    font-size: 10pt;
  }
  .mdc-toolbar__row {
    min-height: 100px;
    padding-top:30px;
    background-image:radial-gradient(150px 150px,rgba(255,255,255,0.6),$mdc-theme-primary);
  }
}
